import { useState } from 'react';
import { Platform } from 'react-native';
import { supabase } from '../utils/supabase';
import { GOOGLE_CLIENT_ID } from '../config/env';

// Get the Google OAuth redirect URL for web
const getRedirectUrl = () => {
  if (Platform.OS !== 'web') {
    console.log('üö´ Not a web platform');
    return undefined;
  }

  // Get the current origin and log all relevant URL information
  const origin = window.location.origin;
  const hostname = window.location.hostname;
  
  console.log('üìç Current URL info:', {
    origin,
    hostname,
    fullUrl: window.location.href
  });
  
  // Map localhost:19006 (Expo dev) to localhost:3000
  if (hostname === 'localhost' || hostname.includes('127.0.0.1')) {
    const redirectUrl = 'http://localhost:3000/auth/callback';
    console.log('üîÑ Development redirect:', redirectUrl);
    return redirectUrl;
  }
  
  // For staging and production, use the actual origin
  const redirectUrl = `${origin}/auth/callback`;
  console.log('üîÑ Production/staging redirect:', redirectUrl);
  return redirectUrl;
};

// Make sure to configure these URLs in Google Cloud Console:
// Development:
// - Origins: http://localhost:19006, http://localhost:3000
// - Redirect: http://localhost:3000/auth/callback
//
// Staging:
// - Origin: https://staging.foilquiver.com
// - Redirect: https://staging.foilquiver.com/auth/callback
//
// Production:
// - Origin: https://foilquiver.com
// - Redirect: https://foilquiver.com/auth/callback

export const useGoogleAuth = () => {
  const [isLoading, setIsLoading] = useState(false);

  const signInWithGoogle = async () => {
    try {
      setIsLoading(true);
      if (Platform.OS !== 'web') {
        console.warn('Mobile authentication not yet implemented');
        return { data: null, error: new Error('Mobile authentication not yet implemented') };
      }

      const redirectTo = getRedirectUrl();
      console.log('üîë Starting Google OAuth with config:', {
        redirectTo,
        provider: 'google'
      });

      const { data, error } = await supabase.auth.signInWithOAuth({
        provider: 'google',
        options: {
          redirectTo,
          queryParams: {
            access_type: 'offline',
            prompt: 'consent',
          },
        },
      });

      if (error) {
        console.error('‚ùå OAuth error:', error);
      } else {
        console.log('‚úÖ OAuth initiated successfully');
      }

      return { data, error };
    } catch (error) {
      console.error('‚ùå Unexpected error:', error);
      return { data: null, error };
    } finally {
      setIsLoading(false);
    }
  };

  return {
    signInWithGoogle,
    isLoading,
  };
}; 